            var instance = this._pool[this._prefixConstant + newsFileId];

            if (instance) {
                instance.setData(newsFileData);
            } else {
                instance = new News();
                instance.setData(newsFileData);
                this._pool[this._prefixConstant + newsFileId] = instance;
            }

            return instance;
